body{
}
.myButton {
  background-color: white;
  font-size: 30px;
  padding: 10px;
  font-family: verdana, sans-serif;
  text-transform: uppercase;
  font-weight: 700;
  color: #ffd000;
  text-shadow: 1px 1px 1px #505050,
  1px 2px 1px #505050,
  1px 3px 1px #505050,
  1px 4px 1px #505050;
  border: 4px solid #ffd000;
  box-shadow: 1px 8px 10px rgba(16,16,16,0.4);
  transition: all .2s  ease 0s;
  &:hover{
    color: white;
    background-color: #ffd000;
    border: 4px solid white;
  }
  &_card{
    position: absolute;
    top: -1%;
    left: -1%;
    font-size: 20px;
  }
}

[class*="__container"]{
  max-width: 1170px;
  margin: 0 auto;
  padding-right: 15px;
  padding-left: 15px;
}